@charset "UTF-8";
/*Recipe Styling Begin here*/
.posttype-wrapper {
	display: flex;
	flex-direction: column;
}

.posttype-wrapper.has-sidebar main#posttype-main {
	flex: 1 0 100%;
}

.posttype-wrapper.has-sidebar aside {
	flex: 1 0 100%;
}

.posttype-wrapper.posttype-layout-right main#posttype-main {
	order: 0;
}

.posttype-wrapper.posttype-layout-right aside {
	order: 1;
}

.posttype-wrapper.posttype-layout-left main#posttype-main {
	order: 1;
}

.posttype-wrapper.posttype-layout-left aside {
	order: 0;
}

.posttype-container {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}

.posttype-container .posttype-section {
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	justify-content: flex-start;
	flex: 1 0 100%;
}

.posttype-container .posttype-sub-section {
	display: flex;
	text-align: center;
}

.posttype-container .posttype-sub-section > div {
	display: flex;
	flex-wrap: wrap;
	flex-grow: 1;
	flex: 1;
	align-items: center;
	justify-content: center;
}

.posttype-container .posttype-sub-section > div:not(:last-of-type) {
	border-right: 2px solid #f1f1f1;
}

.posttype-container .posttype-sub-section > div > span {
	flex-basis: 100%;
}

.posttype-container .posttype-sub-section .subsection-icon {
	flex: 0 0 15%;
}

.posttype-container .recipe-description {
	text-align: unset;
}

.posttype-container .instructions-subsection {
	font-style: italic;
	margin-bottom: 0.5em;
}

.posttype-container .recipe-instructions .instructions-list {
	padding-left: 30px;
	counter-reset: instruction;
	margin-left: 0;
	margin-bottom: 0;
}

.posttype-container .recipe-instructions .instructions-list li {
	padding-left: 0px;
}

.posttype-container .recipe-instructions .recipe-single-instruction {
	display: flex;
	counter-increment: instruction;
	margin-bottom: 5px;
	margin-left: 0;
}

.posttype-container .recipe-instructions .recipe-single-instruction:before {
	margin-right: 10px;
	content: counter(instruction);
	border-radius: 100%;
	width: 1.8em;
	height: 1.8em;
	min-width: 1.8em;
	min-height: 1.8em;
	text-align: center;
	display: inline-block;
}

.posttype-container .recipe-nutrition .nutrition-info-cont {
	display: flex;
	justify-content: space-around;
}

.posttype-section-box {
	width: 100%;
}

@media screen and (min-width: 767px) {
	.posttype-wrapper {
		flex-direction: row;
	}
	.posttype-wrapper.has-sidebar main#posttype-main {
		flex: 1 0 75%;
	}
	.posttype-wrapper.has-sidebar aside {
		flex: 1 0 25%;
	}
	.posttype-container .recipe-nutrition .nutrition-info-cont {
		display: inherit;
		justify-content: unset;
	}
}

.nutrition-facts {
	font-size: small;
	line-height: 1.4;
	border: 1px solid #000;
	margin: 20px 0;
	display: flex;
	flex-direction: column;
	width: 300px;
	padding: 0.5rem;
}

.nutrition-facts p {
	margin: 0;
}

.nutrition-facts table {
	border-collapse: collapse;
}

.nutrition-facts .calories-section {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	border-bottom: 5px solid #000;
}

.nutrition-facts .amount-serving-text {
	border-bottom: 1px solid #000;
	font-size: 0.8em;
}

.nutrition-facts .nutrition-dv-section {
	display: flex;
	justify-content: flex-end;
	font-size: 0.8em;
	font-weight: bold;
	padding: 3px 0;
	border-bottom: 1px solid #000;
}

.nutrition-facts .calories-section {
	padding: 5px 0;
}

.nutrition-facts .nutrition-parent {
	font-weight: bold;
}

.nutrition-facts .nutrition-child {
	padding-left: 20px;
}

.nutrition-facts .nutrition-facts-details {
	border-bottom: 10px solid #000;
}

.nutrition-facts .nutrition-facts-details .nutrition-detail {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	border-bottom: 1px solid #000;
	padding: 3px 0;
}

.nutrition-facts .nutrition-facts-details .nutrition-percent {
	font-weight: bold;
}

.nutrition-facts__title {
	font-weight: bold;
	font-size: 2rem;
	margin: 0 0 0.25rem 0;
}

.nutrition-facts__header {
	border-bottom: 10px solid black;
	padding: 0 0 0.25rem 0;
	margin: 0 0 0.5rem 0;
}

.nutrition-facts__header p {
	margin: 0;
}

.nutrition-facts__table, .nutrition-facts__table--small, .nutrition-facts__table--grid {
	width: 100%;
}

.nutrition-facts__table thead tr th, .nutrition-facts__table--small thead tr th, .nutrition-facts__table--grid thead tr th, .nutrition-facts__table thead tr td, .nutrition-facts__table--small thead tr td, .nutrition-facts__table--grid thead tr td {
	border: 0;
}

.nutrition-facts__table th, .nutrition-facts__table--small th, .nutrition-facts__table--grid th, .nutrition-facts__table td, .nutrition-facts__table--small td, .nutrition-facts__table--grid td {
	font-weight: normal;
	text-align: left;
	padding: 0.25rem 0;
	border-top: 1px solid black;
	white-space: nowrap;
}

.nutrition-facts__table td:last-child, .nutrition-facts__table--small td:last-child, .nutrition-facts__table--grid td:last-child {
	text-align: right;
}

.nutrition-facts__table .blank-cell, .nutrition-facts__table--small .blank-cell, .nutrition-facts__table--grid .blank-cell {
	width: 1rem;
	border-top: 0;
}

.nutrition-facts__table .thick-row th, .nutrition-facts__table--small .thick-row th, .nutrition-facts__table--grid .thick-row th, .nutrition-facts__table .thick-row td, .nutrition-facts__table--small .thick-row td, .nutrition-facts__table--grid .thick-row td {
	border-top-width: 5px;
}

.small-info {
	font-size: 0.7rem;
}

.nutrition-facts__table--small {
	border-bottom: 1px solid #000;
	margin: 0 0 0.5rem 0;
}

.nutrition-facts__table--small thead tr {
	border-bottom: 1px solid black;
}

.nutrition-facts__table--small td:last-child {
	text-align: left;
}

.nutrition-facts__table--small th, .nutrition-facts__table--small td {
	border: 0;
	padding: 0;
}

.nutrition-facts__table--grid {
	margin: 0 0 0.5rem 0;
}

.nutrition-facts__table--grid td:last-child {
	text-align: left;
}

.nutrition-facts__table--grid td:last-child::before {
	content: "•";
	font-weight: bold;
	margin: 0 0.25rem 0 0;
}

.text-center {
	text-align: center;
}

.thick-end {
	border-bottom: 10px solid black;
}

.thin-end {
	border-bottom: 1px solid black;
}

.recipe-wrapper {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}

.boorecipe-recipe .recipe-head {
	padding-bottom: 1em;
}

.boorecipe-recipe .recipe-section-heading {
	margin-bottom: 0.5em;
}

.boorecipe-recipe .recipe-img-cont {
	margin-bottom: 1rem;
}

.boorecipe-recipe .recipe-img {
	max-width: 100%;
}

.boorecipe-recipe .recipe-ingredients {
	margin-bottom: 1em;
}

.boorecipe-recipe .recipe-meta > div {
	padding: 0.5em 0;
}

@media screen and (min-width: 30em) {
	.boorecipe-recipe.ingredient-side .recipe-meta {
		flex: 1 1 50%;
		padding-right: 1em;
	}
	.boorecipe-recipe.ingredient-side .recipe-ingredients {
		padding: 1rem;
		flex: 1 0 40%;
	}
	.boorecipe-recipe.nutrition-side .recipe-instructions {
		flex: 1 1 50%;
		padding-right: 1em;
	}
	.boorecipe-recipe.nutrition-side .recipe-nutrition {
		flex: 1 0 40%;
	}
}

.boorecipe-recipe div#comments {
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	justify-content: flex-start;
	flex: 1 0 100%;
}

.posttype-wrapper {
	/* Side bar Styling */
}

.posttype-wrapper main#posttype-main {
	padding: 1em;
}

.posttype-wrapper #secondary.widget-area {
	padding: 1em;
}

.posttype-wrapper #secondary.widget-area > li {
	list-style: none;
}

/*
  Making iframe responsive
 */
@media screen {
	.responsive-video-container {
		position: relative;
		padding-bottom: 56.25%;
		/* 16:9 */
		padding-top: 25px;
		height: 0;
	}
	.responsive-video-container video,
	.responsive-video-container iframe {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}

.wp-video, video.wp-video-shortcode, .mejs-container, .mejs-overlay.load {
	width: 100% !important;
	height: 100% !important;
}

.mejs-container {
	padding-top: 56.25%;
}

.wp-video {
	max-width: 100% !important;
}

video.wp-video-shortcode {
	max-width: 100% !important;
	position: relative;
}

.mejs-mediaelement {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
}

.mejs-controls {
	display: none;
}

.mejs-overlay-play {
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	width: auto !important;
	height: auto !important;
}

.share-buttons-cont {
	padding: 1em 0;
}

.share-buttons-cont .share-link-button {
	padding: 8px 10px;
	display: inline-flex;
	margin-right: 3px;
	border-radius: 3px;
	line-height: 1em;
	background: #999999;
	color: #fff !important;
	margin-bottom: 6px;
	text-decoration: none;
}

.share-buttons-cont .share-link-button:hover {
	background: #777777;
	color: #ffffff;
	border-color: #777777;
}

.share-buttons-cont .share-link-button span {
	display: flex;
	flex: 1;
}

.share-buttons-cont .share-link-button span.share-icon {
	padding-right: 0.5em;
}

.share-buttons-cont .share-link-button.facebook-share {
	background-color: #3B5998;
}

.share-buttons-cont .share-link-button.twitter-share {
	background-color: #1DA1F2;
}

.share-buttons-cont .share-link-button.linkedin-share {
	background-color: #2274b9;
}

.share-buttons-cont .share-link-button.email-share {
	background-color: #666666;
}

.share-buttons-cont .share-link-button.pinterest-share {
	background-color: #bc0d1e;
}

.share-buttons-cont .share-link-button.google-plus-share {
	background-color: #DD5144;
}

/* Smartphones (portrait and landscape) ----------- */
@media screen and (max-width: 568px) {
	.share-buttons-cont .share-link-button {
		min-width: 65px;
	}
}

.select-items-cont {
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
}

.select-items-cont .select-item {
	display: inline-block;
	cursor: pointer;
	position: relative;
	padding-left: 30px;
	margin-right: 15px;
}

.select-items-cont .select-item.active {
	color: #888;
	text-decoration: line-through;
}

.select-items-cont .select-item::before {
	content: "";
	display: inline-block;
	width: 19px;
	height: 19px;
	margin-right: 10px;
	background-color: #fff;
	border: 2px solid #d0d0d0;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
	vertical-align: middle;
}

.recipe-rating {
	display: flex;
	flex-direction: row-reverse;
	justify-content: flex-end;
	margin-bottom: 1em;
}

.recipe-rating input {
	position: absolute;
	left: -999999px;
}

.recipe-rating label {
	display: inline-block;
	font-size: 0;
}

.recipe-rating > label svg {
	color: #ccc;
}

.recipe-rating > label:hover, .recipe-rating > label:hover ~ label, .recipe-rating > label.selected, .recipe-rating > label.selected ~ label {
	cursor: pointer;
}

.recipe-rating > label:hover svg, .recipe-rating > label:hover ~ label svg, .recipe-rating > label.selected svg, .recipe-rating > label.selected ~ label svg {
	color: #f0ad4e;
	background: -webkit-linear-gradient(-45deg, #fcb551 0%, #d69a45 100%);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

.rating-stars svg {
	color: #f0ad4e;
}

@media print {
	* {
		background: none !important;
		background-color: unset !important;
		color: #000000 !important;
	}
	.single-recipe {
		background: #ffffff;
		color: #000000 !important;
		margin: 0;
		max-width: 100%;
	}
	.single-recipe .recipe-img-cont,
	.single-recipe .responsive-video-container,
	.single-recipe .wp-video,
	.single-recipe .single-featured-image-header,
	.single-recipe .subsection-icon,
	.single-recipe aside#secondary,
	.single-recipe img.avatar,
	.single-recipe .recipe-rating,
	.single-recipe i {
		display: none !important;
	}
}

@media print and (color) {
	* {
		-webkit-print-color-adjust: exact;
	}
}

.recipe-author-box {
	display: flex;
	margin-top: 1em;
	margin-bottom: 1em;
	flex-direction: column;
	flex-wrap: wrap;
	padding: 1em;
	background: #f6f6f6;
}

@media screen and (min-width: 40em) {
	.recipe-author-box {
		flex-direction: row;
		text-align: unset;
	}
	.recipe-author-img {
		flex-basis: 120px;
	}
	.recipe-author-desc {
		flex-basis: calc(100% - 120px);
	}
}

.recipe-author-name-box {
	display: flex;
	margin-right: 1em;
}

.recipe-author-name-box .recipe-author-avatar img {
	flex: 1;
	display: flex;
	margin-right: 0.25em;
}
